/* Custom reset.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a:focus,
a:active a:active:focus,
button:active,
button:active:focus,
button:focus,
input:active,
input:active:focus,
input:focus,
textarea:active,
textarea:active:focus,
textarea:focus,
input,
button,
a,
textarea {
  outline: none !important;
  outline-color: transparent !important;
  outline-width: 0 !important;
  outline-style: none !important;
}

a {
  -webkit-touch-callout: none;
  user-select: none;
}

.document {
  font-size: 62.5%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

ul,
ol {
  list-style-type: none;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  border: none;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

/* Scrollbar styling */
* {
  scrollbar-width: thin;
  scrollbar-color: #6583ED transparent;
}
*::-webkit-scrollbar {
  display: block;
  overflow: auto;
  width: 0.4rem;
  height: 0;
}
*::-webkit-scrollbar-track, *::-webkit-scrollbar-track-piece {
  background-color: transparent;
}
*::-webkit-scrollbar-thumb {
  background-color: #6583ED;
  border-radius: 1rem;
}
*::-webkit-scrollbar-thumb:hover {
  cursor: pointer;
  background-color: #214ce5;
}
*::-webkit-scrollbar-button, *::-webkit-scrollbar-corner, *::-webkit-resizer {
  display: none;
}

::selection {
  background: #6583ED;
}

::-moz-selection {
  background: #6583ED;
}

.document-body {
  min-width: 100vw;
  overflow-x: hidden;
  background: #FFFFFF;
  font-family: "Raleway", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
  color: #343434;
  font-feature-settings: "lnum";
}

/* Frequent classes */
.container {
  margin: 0 auto;
  width: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

.button {
  padding: 18px 32px;
  display: block;
  border: 1px solid #7E7E7E;
  border-radius: 30px;
  background-color: white;
  transition: all 0.2s linear;
  font-size: 18px;
  line-height: 18px;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  color: #7E7E7E;
}
.button:hover {
  cursor: pointer;
  color: #6583ED;
  border-color: #343434;
}
.button:active {
  background-color: #CCD6F8;
}
.button.button--blue {
  color: white;
  background-color: #6583ED;
  border-color: #6583ED;
}
.button.button--blue:hover {
  background-color: white;
  color: inherit;
}

.header-container {
  padding: 24px 0;
  display: flex;
  justify-content: space-between;
}
.header .header-right {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (min-width: 0) {
  .header .header-right {
    gap: 0;
    flex-direction: column;
  }
}
@media screen and (min-width: 992px) {
  .header .header-right {
    gap: 116px;
    flex-direction: row;
  }
}
.header .address_email {
  display: none;
}

.logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.logo__wrapper {
  overflow: hidden;
}
@media screen and (min-width: 576px) {
  .logo__wrapper {
    width: 157px;
    height: 37px;
  }
}
@media screen and (min-width: 992px) {
  .logo__wrapper {
    width: 253px;
    height: 62px;
  }
}

.address {
  flex-direction: column;
  font-style: normal;
  font-size: 20px;
  font-weight: medium;
  text-align: right;
}
@media screen and (min-width: 576px) {
  .address {
    display: none;
  }
}
@media screen and (min-width: 576px) {
  .address {
    display: block;
  }
}

.main {
  padding: 73px 0;
}
.main-container {
  position: relative;
}
.main-header {
  margin-bottom: 40px;
}
.main_title {
  margin-bottom: 23px;
  width: 450px;
  font-size: 48px;
  font-weight: 600;
  line-height: 48px;
}
@media screen and (min-width: 992px) {
  .main_title {
    width: 100%;
  }
}
@media screen and (min-width: 1200px) {
  .main_title {
    width: 100%;
  }
}
.main_subtitle {
  margin-bottom: 38px;
  width: 450px;
  font-size: 18px;
}
.main-controls {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}
@media screen and (min-width: 992px) {
  .main-controls {
    flex-direction: column;
  }
}
@media screen and (min-width: 1400px) {
  .main-controls {
    flex-direction: row;
  }
}
.main_image__wrapper {
  position: absolute;
  z-index: -1;
  top: -30px;
  right: -50px;
  aspect-ratio: 1256/755;
  height: 650px;
  transition: all 0.2s linear;
  opacity: 1;
}
.main_image__wrapper.clear {
  opacity: 0.1;
}
@media screen and (min-width: 992px) {
  .main_image__wrapper {
    top: 40px;
    height: 400px;
  }
}
@media screen and (min-width: 1200px) {
  .main_image__wrapper {
    top: 30px;
    height: 600px;
  }
}
@media screen and (min-width: 1600px) {
  .main_image__wrapper {
    top: -84px;
    height: 700px;
  }
}
@media screen and (min-width: 1950px) {
  .main_image__wrapper {
    top: -84px;
    height: 755px;
  }
}
.main_image, .main_image--state {
  position: absolute;
  visibility: hidden;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.main_image.visible, .main_image--state.visible {
  visibility: visible;
}
.main-cards {
  display: grid;
  grid-template-rows: 1fr;
  gap: 18px;
}
@media screen and (min-width: 1200px) {
  .main-cards {
    grid-template-areas: "A . ." "B . ." "C D E";
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (min-width: 992px) {
  .main-cards {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "A ." "B C" "D E";
  }
}
.main-card {
  position: relative;
  padding: 22px 30px;
  border: 1px solid #343434;
  border-radius: 30px;
  overflow: hidden;
  background-color: #fff;
  font-size: 21px;
  font-weight: 600;
}
@media screen and (min-width: 1200px) {
  .main-card {
    font-size: 18px;
  }
}
.main-card_text {
  position: relative;
  z-index: 1000;
}
.main-card_text b {
  display: inline;
  font-size: 38px;
  font-weight: 700;
}
.main-card:nth-child(1) {
  grid-area: A;
}
.main-card:nth-child(2) {
  grid-area: B;
}
.main-card:nth-child(3) {
  grid-area: C;
}
.main-card:nth-child(4) {
  grid-area: D;
}
.main-card:nth-child(5) {
  grid-area: E;
}
.main-card_number {
  position: absolute;
  bottom: -25%;
  left: -5%;
  z-index: 100;
  font-size: 120px;
  line-height: 120px;
  font-weight: 900;
  color: rgba(204, 214, 248, 0.5);
}

.infrastructure {
  margin-top: 40px;
}
.infrastructure-container {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}
@media screen and (min-width: 576px) {
  .infrastructure-container {
    flex-direction: column;
  }
}
@media screen and (min-width: 992px) {
  .infrastructure-container {
    flex-direction: row;
    align-items: center;
  }
  .infrastructure:nth-child(2n+1) .infrastructure-container {
    flex-direction: row-reverse;
  }
}
.infrastructure_image {
  position: absolute;
  object-fit: contain;
}
.infrastructure_image__wrapper {
  position: relative;
  display: flex;
  height: 600px;
}
@media screen and (min-width: 576px) {
  .infrastructure_image__wrapper {
    flex: 40%;
    overflow: hidden;
  }
}
@media screen and (min-width: 992px) {
  .infrastructure_image__wrapper {
    flex: 50%;
    aspect-ratio: 950/600;
  }
}
@media screen and (min-width: 576px) {
  .infrastructure_image {
    top: 50%;
    left: -140px;
    transform: translateY(-50%);
    width: auto;
    height: 100%;
  }
}
@media screen and (min-width: 1200px) {
  .infrastructure_image {
    top: 0;
    left: 0;
    transform: translateY(0);
    width: 100%;
    height: 100%;
  }
}
@media screen and (min-width: 576px) {
  .infrastructure-content {
    flex: 60%;
  }
}
@media screen and (min-width: 1200px) {
  .infrastructure-content {
    flex: 50%;
  }
}
.infrastructure_title {
  margin-bottom: 30px;
  font-size: 48px;
  font-weight: 600;
}
.infrastructure-filling {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.infrastructure-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.infrastructure-list_enumeration {
  margin-left: 50px;
  position: relative;
}
.infrastructure-list_enumeration::before {
  content: "";
  position: absolute;
  top: calc(50% - 6px);
  left: -30px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #6583ED;
}

.partners-container {
  padding-top: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.partners_title {
  margin-bottom: 55px;
  text-align: center;
  font-size: 48px;
  font-weight: 600;
  color: #7E7E7E;
}
.partners-list {
  padding: 65px 90px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  gap: 30px;
  width: 80%;
  border: 1px solid #343434;
  border-radius: 30px;
}
.partners-list_enumeration {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-container {
  padding: 80px 0 31px;
}
.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.rights {
  padding: 28px 0;
  display: flex;
  justify-content: center;
  border-top: 1px solid #6583ED;
}

/* Adaptive */
@media screen and (min-width: 576px) {
  .container {
    width: 540px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 720px;
  }
}
@media screen and (min-width: 992px) {
  .container {
    width: 960px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    width: 1140px;
  }
}
@media screen and (min-width: 1400px) {
  .container {
    width: 1320px;
  }
}
@media screen and (min-width: 1500px) {
  .container {
    width: 1420px;
  }
}
@media screen and (min-width: 1600px) {
  .container {
    width: 1520px;
  }
}
@media screen and (min-width: 1950px) {
  .container {
    width: 1920px;
  }
}